.container {
    padding: 20px;
    background-color: #111;
  }
  
  h2 {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .viewModeToggle {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .viewModeToggle button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    background-color: #333;
    color: white;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .viewModeToggle button:hover {
    background-color: #444;
  }
  
  .viewModeToggle button.activeViewMode {
    background-color: #e50914;
  }
  
  .tableView {
    text-align: center;
  }
  
  .movieTable {
    width: 100%;
    border-collapse: collapse;
  }
  
  .movieTable th, .movieTable td {
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  .movieTable th {
    background-color: #333;
    color: white;
  }
  
  .movieTable td img {
    width: 100px;
    cursor: pointer;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
  
  .pagination button {
    padding: 10px;
    font-size: 14px;
    cursor: pointer;
    border: none;
    background-color: #333;
    color: white;
    border-radius: 4px;
  }
  
  .pagination button:disabled {
    background-color: #888;
  }
  
  .infiniteScroll ul {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .infiniteScroll li {
    list-style: none;
    width: 200px;
    text-align: center;
  }
  
  .infiniteScroll img {
    width: 100%;
    cursor: pointer;
  }
  
  .topButton {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #e50914;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    font-size: 20px;
    display: none;
    transition: background-color 0.3s;
  }
  
  .topButton:hover {
    background-color: #b20710;
  }
  
  .movieModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modalContent {
    background-color: rgb(53, 53, 53);
    padding: 20px;
    display: flex;
    gap: 20px;
    align-items: center;
    position: relative;
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    color: rgb(216, 216, 216);
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  .modalPoster {
    width: 150px;
  }
  
  .modalInfo {
    max-width: 500px;
    line-height: 1.6;
  }
  
  .modalInfo h3 {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .modalInfo p {
    margin-bottom: 10px;
  }
  